.search-results {
  display: flex;
  align-items: center;
}

.message {
  min-width: 420px;
  min-height: 410px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  p {
    width: 30ch;
    text-align: center;
  }

  img {
    height: 200px;
    margin-bottom: 1rem;
    opacity: 0.5;
  }
}

@media screen and (max-width: 768px) {
  .search-results {
    flex-direction: column;
  }
}
