.forecast {
  display: grid;
  grid-template: 1fr 1fr / 1fr 1fr;
  margin-top: 1rem;

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    img {
      transform: scale(75%);
    }

    span {
      position: absolute;
    }
  }

  &__date {
    top: 20px;
  }

  &__temp {
    bottom: 20px;
  }

  &__item:hover {
    background-color: #3a3f74;
    border-radius: 7px;
  }
}

@media screen and (max-width: 530px) {
  .forecast img {
    transform: scale(50%);
  }

  .forecast__item {
    width: 100px;
  }
}
